<template>
<div class="switch-button" :class="{'active': value}">
  <div class="ball-box" @click="change_fn">
    <div class="ball"></div>
  </div> {{label}}
</div>
</template>

<script>
/**  options 
    value      [boolean]     true or false, false
    label      [string]     
    callback   [function]   callback
*/
export default {
  name: 'SwitchButton',
  props: ['options'],
  data () {
    return {
      value: null,
      label: ''
    }
  },
  methods: {
    change_fn() {
      this.value = !this.value;
      if (typeof this.options.callback === 'function') {
        this.options.callback(this.value);
      }
    }
  },
  created() {
    this.value = (this.options.value)? this.options.value : false;
    this.label = (this.options.label)? this.options.label : '';
  }
}
</script>

<style lang="scss" scoped>
    @import './style.scss';
</style>


