<template>
<div class="tree-menu" :class="{root: (depth === 0)}">
  <div class="label-box">
    <i class="icon fa " aria-hidden="true"
      :class="[icons, noNext]"
      @click="toggle()"
      ></i>
    <span>{{ label }}</span> 
  </div>

  <!--  -->
  <TreeMenu v-for="(item, i) in nodes" v-show="opens"
    :nodes="item.nodes" 
    :label="item.label" 
    :depth="(depth + 1)"
    :open="opens"
    :key="'depth'+ (depth + i)" ></TreeMenu>

</div>
</template>

<script>
/**  
    nodes  [Array]   
    label  [String]  
    depth  [Number]  
    open   [Boolean] 
*/
export default {
  name: 'TreeMenu',
  props: ['nodes', 'label', 'depth', 'open'],
  data () {
    return {
      opens: null
    }
  },
  methods: {
    toggle() {
      if(this.noNext) {
        return false;
      }
      this.opens = !this.opens;
    }
  },
  computed: {
    icons() {
      let re = (this.opens)? 'fa-minus-square-o' : 'fa-plus-square-o';
      re = (this.nodes)? re : '';
      return re;
    },
    noNext() {
      return (!this.nodes)? 'no-next-icon fa-minus-square-o' : '';
    }
  },
  created() {
    if (!this.open || !this.nodes) {
      this.opens = false;
    }
  }
}
</script>

<style lang="scss" scoped>
  @import './style.scss';
</style>


